.post-left,
.post-right {
  position: relative;
  float: left;
  width: 50%;
  padding-right: 13px;
  height: 100%;
}
article {
  margin: 0;
  background: transparent;
}
.post-left-scroll,
.post-right-scroll {
  background: rgba(0, 0, 0, 0.2);
  min-height: 100%;
}
.post-right-scroll {
  background: rgba(0, 0, 0, 0.2);
  height: 100%;
  display: flex;
  flex-direction: column;
}
textarea {
  flex: 1;
  font-size: 13px;
}
.bottom-line {
  margin: 0;
}
.bottom-line.mb10 {
  margin-bottom: 10px;
}
.footer .box-label {
  padding-left: 10px;
}
.box-list {
  margin-bottom: 10px;
}
